<template>
 <div class='booklist'>
  <div  v-for="item in booklist">
    <router-link to=''>
   
       <span>【{{item.author}}】</span>
       <p>{{item.shortdesc}}</p>
     
    </router-link>
   </div>
 </div>
</template>
<script>

export default {
  props: ['booklist'],
  name: 'booklist',
  data () {
    return {
    }
  },
  mounted () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.booklist{
    padding:0 .5rem;
    font-size:.6rem;
    div{
        overflow:hidden;
        margin-bottom:.5rem;
        a{
            display:block;
            overflow:hidden;

            span{
                display:block;
                width:auto;
                overflow:hidden;
                float:left;
                position:relative;
                color:#a3a3a3;
            }
            p{
                max-width:80%;
                overflow:hidden;
                text-overflow:ellipsis;
                white-space:nowrap;
                color:black;
            }
        }

    }
}
         
</style>



