<template>
 <div class="btn">
     <ul >
         <li v-for="(item,index) in btn" >
         <router-link :class="item.color"  to=''>{{item.btn}}</router-link>
         </li>
     </ul>
 </div>
</template>
<script>

export default {
  props: ['btn'],
  name: 'nav',
  data () {
    return {
    }
  },
  mounted () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.btn{
    width: 100%;
    border-bottom: 1px solid #f0f1f2;
    ul{
        display:flex;
        display: -weblit-box;
        display: box;
        display: -webkit-flex;
        display: -moz-box;
        display: -ms-flexbox;
        box-pack:justify;
        -webkit-box-pack:justify;
        justify-content:space-between;
        padding:.75rem .65rem;
        li{
            width:4.4rem;
            height:1.35rem;
           
            a{
              display:block;
              width:100%;
              height:100%;
              font-size:.55rem;
              text-align:center;
              line-height:1.35rem;
              border:1px solid #f0f1f2;
              border-radius:.2rem;
            }
        }
    }
}
.red{
    background-color:#FF5C4D;
    color: white;
}         
</style>



