<template>
   <div class="swiper-container">
      <div class="swiper-wrapper">

        <div class="swiper-slide" v-for="item in indexImg || DetialImg || listImg">
          <img class="imgload" :src='item.url' alt="首页banner图片01">
        </div>
        
      </div>
      <!-- 如果需要分页器 -->
      <div class="swiper-pagination"></div>

      <!-- 如果需要导航按钮 -->
     
    </div>
</template>
<script>
import '../../../static/swiper/swiper.min.css'
import '../../../static/swiper/swiper.min.js'
export default {
  props: ['indexImg', 'listImg', 'DetialImg'],
  name: 'swiper',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted () {
    Swiper('.swiper-container', {
      pagination: '.swiper-pagination',
      paginationClickable: true,
      loop: true,
      speed: 600,
      autoplay: 1000,
      autoplayDisableOnInteraction: false
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.swiper-container {
    width: 100%;
    height:5.2rem;
    background-color:red;
    .swiper-wrapper {
        width: 100%;
        height: 100%;
    }
    .swiper-slide {
        background-position: center;
        background-size: cover;
        width: 100%;
        height: 100%;
        img {
            width: 100%;
            height: 100%;
        }
    }
    .swiper-pagination-bullet {
        width:20px;
        height:20px;
        background-color:blue;
        display: inline-block;
        background: #7c5e53;
    }
}
         
</style>



