<template>
 <!--文本描述-->
    <div class="description" >
      <div class='descriptionwrap' v-for="item in description">
      <router-link to=''>
      <div class="desleft">
                <img :src='item.avartar' alt="">
            </div>
            <div class="desright">
                <p class='title'>{{item.title}}</p>
                <p class='des' @click='text()'>{{item.dess}}</p>
                <div>
                   <span><em class='gray'>{{item.sta1}}</em>
                   <em class='red'>{{item.sta2}}</em>
                   <em class='green'>{{item.textnum}}</em></span>
                </div>
            </div>
      </router-link>
            
      </div>
        
    </div>
</template>
<script>

export default {
  props: ['description'],
  name: 'description',
  data () {
    return {
    }
  },
//  钩子函数 先
  mounted: function () {
    this.text()
  },
  methods: {
    text: function () {
      this.description.map(function (v, i) {
        if (v.dess.length >= 40) {
          var textnew = v.dess.substring(0, 40) + '...'
          v.dess = textnew
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.description{
    width: 100%;
    div:last-child{
        border-bottom: none;
    }
    .descriptionwrap{
        width:100%;
        height: auto;
        overflow: hidden;
        padding:.5rem;
        border-bottom: 1px solid #d7d7d7;
        .desleft{
            float:left;
            position: relative;
            width:3.5rem;
            height:5.0rem;
            margin-right:.45rem;
            img{
               width: 100%;
               height:100%;
            }
          
        }
        .desright{
            overflow: hidden;
            .title{font-size:.75rem}
            .des{
                font-size:.55rem;
                min-height:2.75rem;
                color:#999999;
            }
            div{
                overflow:hidden;
                p{
                    float:left;
                    max-width:40%;
                    overflow:hidden;
                    white-space: nowrap;
                    text-overflow:ellipsis;
                   font-size:.45rem;
                  background-color:yellow;
                  position:relative;   
                }
                span{
                    float: right;
                    display:block;
                   position:relative;
                    em{
                        position:relative;
                        padding:0.05rem 0.1rem;
                        font-size:9px;
                        font-style:normal;
                         &::before {
                            content: '';
                            border:1px solid;
                            position: absolute;
                            top: -1px;
                            bottom:1px;
                            left:0;
                            right:0;
                            border-radius:.1rem;
                        }
                    }                    
                }
            }
        }
    }
}
.gray{
    color:#999999;
}
.red{
    color:#ff5c4d;
}  
.green{
    color:#08b295;
}       
</style>



