<template>
 <div class="footer">
    <div class="download">
        <img src="../../../static/images/img/phone.png" alt=""><span>客户端下载</span>
    </div>
    <!--左侧信息展示-->
    <h2>客服</h2>
    <div class="message">
        
        <p>工作时间：周一到周五9:00-18:00</p>
        <p>客服邮箱：354654364@qq.com</p>
        <p>客服QQ:354654643</p>
        <p>微信公众号：超越小说</p>
    </div>
    <!--右侧二维码-->
    <div class="wc">
        <img :src='a' alt="">
    </div>
 </div>
</template>
<script>
import imgs from '../../../static/images/img/weixin.png'
export default {
  props: [],
  name: 'nav',
  data () {
    return {
      a: imgs
    }
  },
  mounted () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.footer{
    width:100%;
    padding: .75rem;
    overflow: hidden;
    font-size: .55rem;
    line-height: 1.1rem;
    .download{
        width:100%;
        height:1.5rem;
        line-height:1.5rem;
        margin:0 auto;
        text-align:center;
        border:1px solid #c0c0c0;
        border-radius:.2rem;
        font-size:.75rem;
       vertical-align: middle;
       margin-bottom:.75rem;
        img{
            width:.5rem;
            height:.75rem;
            vertical-align: middle;
            margin-top:-0.12rem;
            margin-right:0.5rem
        }
    }
    .message{
        width:60%;
        float: left;
    }
    .wc{
        float: right;
        img{
            width:100px;
            height:100px;
           
        }
    }
}
         
</style>



