<template>
 <div class="nav">
     <ul >
         <li v-for="(item,index) in nav || comicnav">
         <router-link to=''>
            <img :src='item.source' alt="">
             <span>{{item.text}}</span>
         </router-link>
             
         </li>
     </ul>
 </div>
</template>
<script>

export default {
  props: ['nav', 'comicnav'],
  name: 'nav',
  data () {
    return {
    }
  },
  mounted () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.nav{
    width: 100%;
    // height: 3.5rem;
    border-bottom: 1px solid #f0f1f2;
    ul{
        display:flex;
        display: -weblit-box;
        display: box;
        display: -webkit-flex;
        display: -moz-box;
        display: -ms-flexbox;
        box-pack:justify;
        -webkit-box-pack:justify;
        justify-content:space-between;
        padding:.45rem .75rem;
        li{
            width:1.75rem;
            font-size:.5rem;
            text-align:center;
            img{
                width:1.75rem;
                height:1.75rem;
            }
        }
    }
}
         
</style>



