<template>
  <div class="index">
    <!--头部-->
   <div class="header">
     <div class="logo"><p>平治文学</p></div>
     <div class="noval-type">
       <div @click='tabsShow(indexs)' v-for="(item,indexs) in tabs"  :class='{active:item.iscur}'> 
         {{item.tabname}} 
        </div>
     </div>
     <div class="noval-nav">
       <router-link class="navlist" to='/'></router-link>
       <router-link class="navlist" to='/detail'></router-link>
     </div>
   </div>
   <!--搜索-->
   <div class="searching">
     <div>
       <router-link to=''>
        <img src="../../../static/images/img/magnifying_glass.png" alt="">
       <span>美人心计</span>
       </router-link>
      
     </div>
   </div>
   <!--轮播组件-->
   <banner :indexImg='indexImg'></banner>
   <!--导航组件-->
   <navs :nav='nav'></navs>
   <!--最近阅读-->
   <div class="latestread">
     <router-link to=''>
       <img src="../../../static/images/img/tipimg.png" alt="">
       <span>|</span>  <span>美人心计</span>
     </router-link>

   </div>
   <!--分割线-->
   <div class="divider"></div>


   <!--热门推荐-->
   <div>
     <titles :title='title.title1'></titles>
    <description :description='Book.des.book'></description>
    <booklist :booklist='datas'></booklist>
   </div>
    <!--分割线-->
   <div class="divider"></div>


    <!--限时免费-->
  <div>
    <titles :title='title.title2'></titles>
    <book :book='Book.free.book'></book>
  </div>
      <!--分割线-->
  <div class="divider"></div>


   <!--大图广告-->
    <div class="advertisement">
      <router-link to=''>
        <img :src='Book.guanggao.avartar' alt="">
      </router-link>
    </div>
      <!--分割线-->
  <div class="divider"></div>


   <!--潜力新作-->
   <div class="Newpotential">
      <titles :title='title.title3'></titles>
     <book :book='Book.free.book'></book>
     <booklist :booklist='Book.des.booklist'></booklist>
   </div>
    <!--分割线-->
   <div class="divider"></div>


   <!--主编力推-->
   <div>
      <titles :title='title.title4'></titles>
       <description :description='Book.Editor.book'></description>
   </div>
   <!--分割线-->
   <div class="divider"></div>

  
  <!--热销专区-->
  <div class="hot">
     <titles :title='title.title5'></titles>
     <book :book='Book.hot.book'></book>
  </div>
  <!--分割线-->
   <div class="divider"></div>
   

  <!--听书专区-->
  <div class="Listening">
    <titles :title='title.title6'></titles>
    <book :book='Book.Listen.book'></book>
    <router-link class="enter" to=''>进入专区</router-link>
  </div>
  <!--分割线-->
   <div class="divider"></div>
  
   <div>
    <input type="text" v-model='msg' ><button v-on:click="ch">留言</button>
    <button v-on:click="dele">删除</button>
    <ul>
      <li v-for="items in item">
          <span>{{items.num}}</span><span>{{items.text}}</span>
      </li>
    </ul>
   </div>



  </div>

</template>

<script>
const APIPROXY = 'https://bird.ioliu.cn/v1/?url='
import Banner from '../templete/swiper'
import Detail from '../detail/detail'
import Nav from '../templete/nav'
import Title from '../templete/title'
import Description from '../templete/description'
import Booklist from '../templete/booklist'
import Books from '../templete/books'
// import Footer from '../templete/footer'
import { page } from '../../assets/js/common.js'
import axios from 'axios'
export default {
  name: 'Index',
  mounted: function () {
    this.getdata()
  },
  data () {
    return {
      datas: '',
      apiUrl: 'http://localhost/VUE/data/data.json',
      link: [
        {links: 'http://m.menghuibook.com/'},
        {links: 'http://mt.cyread.cn/'}
      ],
      img: {},
      msg: 'Welcome',
      indexImg: this.$store.state.indexImg,
      listImg: this.$store.state.listImg,
      DetialImg: this.$store.state.DetialImg,
      Book: this.$store.state,
      tabs: [
        {tabname: '男生', iscur: true, nowShow: 'list'},
        {tabname: '女生', iscur: false, nowShow: 'detail'}
      ],
      currentView: 'list',
      nav: [
         {source: './static/images/img/clock.png', text: '限免'},
         {source: './static/images/img/ranking.png', text: '排行'},
         {source: './static/images/img/bookcase.png', text: '书库'},
         {source: './static/images/img/katong.png', text: '漫画'},
         {source: './static/images/img/recharge1.png', text: '充值'}
      ],
      title: {
        title1: '热门推荐',
        title2: '限时免费',
        title3: '潜力新作',
        title4: '主编力推',
        title5: '热销专区',
        title6: '听书专区'
      },
      item: [
        {'num': '1', 'text': '测试'},
        {'num': '2', 'text': '喔喔'},
        {'num': '3', 'text': '丫丫'}
      ]
    }
  },
  components: {
    'banner': Banner,
    'detail': Detail,
    'navs': Nav,
    'titles': Title,
    'description': Description,
    'booklist': Booklist,
    'book': Books
  },
  methods: {
    a: function () {
      page.init()
    },
    b: function () {

    },
    tabsShow: function (indexs) {
      var x = this
      this.tabs.map(function (v, i) {
        if (i === indexs) {
          v.iscur = true
          window.location.href = x.link[indexs].links
          // x.currentView = v.nowShow
        } else {
          v.iscur = false
        }
      })
    },
    getdata: function () {
      var thats = this
      console.log(thats)
      axios.get(APIPROXY + 'http://iftest.lsread.com/Product/List.aspx')
      .then(function (response) {
        console.log(response)
        thats.datas = response.data.list.item
      })
      .catch(function (error) {
        console.log(error)
      })
    },
    ch: function () {
      var number = this.item.length
      number++
      this.item.push({
        'num': number,
        'text': this.msg
      })
    },
    dele: function () {
      this.item.pop()
    }
  },
  computed: {
  }
}
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped lang="less">
@import '../../assets/css/common';
@import './index';
.active{
  background-color: red;
  color: white;
}
.tabs{
  display: flex;
  justified-content: space-between;
}

.hasicon1{
  background: url(../../../static/images/img/tipimg.png) no-repeat;
  background-size:auto 100%;

}
.hasicon2{
  background: url(../../../static/images/img/latestread.png) no-repeat;
  background-size:auto 100%;

}


</style>



