<template>
  <div class="detail">
  <!--头部组件-->
    <heads></heads>
<!--书本基本信息-->
<detailbook></detailbook>
<!--按钮-->
<btn :btn='btn'></btn>

<!--文本描述-->
<div class="bookdescript">
      <p id='a'></p>

    <span v-show='shows' class="show" @click='showtext()'>展开</span>
      
</div>

<!--目录-->
<div>
   <titles :title='title.title1'></titles>
   <catalogbook></catalogbook>
</div>

    <!--猜你喜欢-->
    <div>
      <titles :title='title.title5'></titles>
      <book :book='Book.Listen.book'></book>
    </div>
    <!--分割线-->
   <div class="divider"></div>

   <div>
    <div>{{msg}}</div>
   </div>
  </div>

</template>

<script>
import bus from '../../assets/js/common.js'
import Head from './head'
import detailbook from './detail-book'
import Title from '../templete/title'
import Books from '../templete/books'
import Btn from '../templete/btn'
import Catalogbook from '../detail/Catalog-new'
// import stores from '../vuex/store'
// console.log(this.detaildata)
export default {
  name: 'Detail',
  data () {
    return {
      msg: '',
      detaildata: '',
      shows: true,
      msgs: 'Welcome to Your Vue.js App',
      DetialImg: this.$store.state.DetialImg,
      Book: this.$store.state,
      comicnav: [
        {source: './static/images/img/clock.png', text: 'comic限免'},
        {source: './static/images/img/clock.png', text: 'comic限免'},
        {source: './static/images/img/clock.png', text: 'comic限免'},
        {source: './static/images/img/clock.png', text: 'comic限免'}
      ],
      title: {
        title1: '目录',
        title2: '捧场总数',
        title3: '作者的话',
        title4: '书评区',
        title5: '猜你喜欢'
      },
      btn: [
        {'btn': '开始阅读', 'color': 'red'},
        {'btn': '加入书架'},
        {'btn': '全本优惠'}
      ],
      text: {
        'text': '僻静街道旁的一家杂货店，只要写下烦恼投进前门卷帘门的投信口,第二天就会在店后的牛奶箱里得到回答：因男友身患绝症僻静街道旁的一家杂货店，只要写下烦恼投进店前门卷帘门的投信口，第二天就会在店后的牛奶箱里得到回答：因男友身患绝症'
      }
    }
  },
  created: function () {
    bus.$on('change', function () {
      this.msg = 'hehe'
    })
  },
  beforeCreate: function () {
    this.$http.jsonp('http://localhost/data/new_file.php', {}, {headers: {}, emulateJSON: true})
      .then(function (response) {
        alert('123')
        var datass = response.data.detail
        this.detaildata = datass
        return this.detaildata
      }, function (response) {
      })
    this.$http.jsonp('https://api.douban.com//v2/movie/top250', {},
      {
        headers: {},
        emulateJSON: true }).then((response) => {
          this.movie = response.data
          console.log(this.movie)
        }
    )
  },

  components: {
    'heads': Head,
    'titles': Title,
    'book': Books,
    'detailbook': detailbook,
    'btn': Btn,
    'catalogbook': Catalogbook
  },
  mounted: function () {
    this.showtexts()
    this.datass()
  },
  methods: {
    showtexts: function () {
      var text = this.text.text
      // console.log(text)
      if (text.length > 60) {
        var a = text.substring(0, 60) + '...'
        console.log(a)
        document.getElementById('a').innerHTML = a
      }
    },
    showtext: function () {
      this.shows = !this.shows
      document.getElementById('a').innerHTML = this.text.text
    },
    datass: function () {
    }
  }
}
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@import '../../assets/css/common';
.bookdescript{
  padding: .75rem .5rem;
  font-size: .6rem;
  overflow:hidden;
  position:relative;
  .show{
   position:absolute;
   right:1rem;

  }
}
</style>



