<template>
 <div class="heads">
   <div class="back" @click='history()'>
       <img src="../../../static/images/img/backblack.png" alt="">
   </div>
   <div class="title">
      <p>解忧杂货店解忧杂货店解忧杂货店vv解忧杂货店解忧杂货店</p>
   </div>
   <div class="headnav">
     <router-link to=''>
        <img src="../../../static/images/img/house.png" alt="主页">
     </router-link>
     <router-link to=''> <img src="../../../static/images/img/bookcase1.png" alt="个人中心">
     </router-link>
   </div>
 </div>
</template>
<script>
import imgs from '../../../static/images/img/weixin.png'
export default {
  props: [],
  name: 'nav',
  data () {
    return {
      a: imgs
    }
  },
  mounted () {
  },
  methods: {
    history: function () {
      window.history.back()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.heads{
  height:2rem;
  border-bottom: 1px solid #d7d7d7;
  font-size:.75rem;
  position:relative;
  .back{
    height: 100%;
    width:30%;
    position:absolute;
    left:0.5rem;
    top:0;
    padding-top:0.5rem;
    img{
      width:.4rem;
      height:.7rem;
    }
  }
  .title{
    width:100%;
    height: 100%;
    p{
      width:50%;
      height:100%;
      text-align: center;
      line-height: 2rem;
      overflow: hidden;
      margin:0 auto;
      text-overflow: ellipsis;
      white-space : nowrap;
    }
  }
  .headnav{
    width:30%;
    height:100%;
    position: absolute;
    right:0.5rem;
    top:0;
    text-align: right;
     padding-top:0.5rem;
     a:first-child{
       margin-right:.5rem;
     }
    img{
       width:.75rem;
       height:.7rem;
    }
  }
}    
</style>



