<template>
 <!--标题-->
      <div class="title" >
        <span class="redicon"></span>
        <span>{{title}}</span>
      </div>
</template>
<script>

export default {
  props: ['title'],
  name: 'title',
  data () {
    return {
    }
  },
  mounted () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.title{
    height: 2.25rem;
    line-height: 2.25rem;
    font-size: .75rem;
    font-weight: 400;
    .redicon{
        display: inline-block;
        width:5px;
        height:0.75rem;
        background-color: #ff5c4d;
        vertical-align: middle;
        margin-top:-.2rem;
        margin-right:.4rem
    }
}

         
</style>



