import Vue from 'vue'
import Router from 'vue-router'
// 引入路由对应的组件
import Index from '@/components/index/index.vue'
import Detail from '@/components/detail/detail.vue'
// 全局使用路由
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/detail',
      name: 'Detail',
      component: Detail
    }
  ]
})




